@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Josefin Sans", sans-serif; }

body {
  font-size: 14px;
  height: 100%;
  width: 100%;
  font-family: "Josefin Sans", sans-serif; }

.hidden {
  display: none; }

.container-wrapper {
  width: 100%;
  height: 100%;
  background-image: url(/static/media/bg-desktop-dark.73e47dbb.jpg);
  background-size: 100% 26%;
  background-repeat: no-repeat; }
  .container-wrapper .container {
    width: 450px;
    height: 100vh;
    margin: 0 auto; }
    .container-wrapper .container .header {
      width: 100%;
      display: flex;
      justify-content: space-between;
      padding-top: 60px;
      padding-bottom: 40px; }
      .container-wrapper .container .header h1 {
        text-transform: uppercase;
        letter-spacing: 10px; }
      .container-wrapper .container .header img {
        height: 24px;
        width: 24px; }
    .container-wrapper .container .add-todo-container {
      border-radius: 5px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      padding: 20px;
      margin-bottom: 30px; }
      .container-wrapper .container .add-todo-container input {
        margin-left: 10px;
        padding-top: 5px;
        flex-grow: 1;
        background-color: transparent;
        border: none;
        outline: none; }
    .container-wrapper .container .todo-list-container {
      border-radius: 5px 5px 0 0; }
      .container-wrapper .container .todo-list-container .todo-list-item {
        display: flex;
        justify-content: space-between;
        padding: 20px 20px 5px 20px; }
        .container-wrapper .container .todo-list-container .todo-list-item label {
          font-size: 14px; }
        .container-wrapper .container .todo-list-container .todo-list-item .delete svg {
          width: 18px;
          height: 18px; }
    .container-wrapper .container .todo-filters-container {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      width: 100%;
      padding: 10px 20px;
      border-radius: 0 0 5px 5px; }
      .container-wrapper .container .todo-filters-container .filters a {
        font-weight: bold; }
      .container-wrapper .container .todo-filters-container a {
        text-decoration: none;
        padding-left: 5px;
        padding-right: 5px; }
    .container-wrapper .container footer {
      text-align: center;
      margin-top: 50px; }
      .container-wrapper .container footer p {
        margin-top: 10px; }

.custom-checkbox {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none; }
  .custom-checkbox input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0; }
    .custom-checkbox input:checked ~ .checkmark {
      background: linear-gradient(120deg, #57ddff, #c058f3); }
      .custom-checkbox input:checked ~ .checkmark:after {
        display: block; }
  .custom-checkbox .checkmark {
    position: absolute;
    top: -5px;
    left: 0;
    height: 24px;
    width: 24px;
    border-radius: 50%; }
    .custom-checkbox .checkmark:after {
      left: 9px;
      top: 5px;
      width: 5px;
      height: 10px;
      content: "";
      position: absolute;
      display: none;
      border: solid white;
      border-width: 0 1px 1px 0;
      transform: rotate(45deg); }

